"use client";
import { useState, useEffect } from "react";
import {
  Location,
  WeatherData,
  ForecastData,
  Forecast,
} from "../types/weather-types";
import LightModeButton from "./LightModeButton/LightModeButton";
import Image from "next/image";
import classes from "./Weather.module.css";
export default function Weather({
  theme,
  toggleTheme,
}: {
  theme: string;
  toggleTheme: () => void;
}) {
  const [location, setLocation] = useState<Location>({ lat: 0, lon: 0 });
  const [weather, setWeather] = useState<WeatherData | null>(null);
  const [forecast, setForecast] = useState<ForecastData | null>(null);
  const [error, setError] = useState<string>("");
  const [isCelsius, setIsCelsius] = useState(true);
  useEffect(() => {
    if ("geolocation" in navigator) {
      navigator.geolocation.getCurrentPosition(
        (position) => {
          setLocation({
            lat: position.coords.latitude,
            lon: position.coords.longitude,
          });
        },
        () => {
          setError("Unable to retrieve location. Please enter a city.");
        }
      );
    } else {
      setError("Geolocation is not supported by this browser.");
    }
  }, []);

  useEffect(() => {
    if (location.lat !== 0 && location.lon !== 0) {
      getWeatherByLocation(location.lat, location.lon);
    }
  }, [location]);

  const getWeatherByLocation = async (lat: number, lon: number) => {
    try {
      setError("");
      const [weatherRes, forecastRes] = await Promise.all([
        fetch(`/api/weather?lat=${lat}&lon=${lon}`),
        fetch(`/api/forecast?lat=${lat}&lon=${lon}`),
      ]);

      if (!weatherRes.ok || !forecastRes.ok) {
        throw new Error("Failed to fetch data");
      }

      const weatherData: WeatherData = await weatherRes.json();
      const forecastData: ForecastData = await forecastRes.json();

      setWeather(weatherData);
      setForecast(forecastData);
      console.log(forecast);
      debugger;
    } catch {
      setError("Failed to fetch weather data.");
    }
  };

  const convertCelsiusToFahrenheit = (celsius: number) => {
    return Math.round((celsius * 9) / 5 + 32);
  };

  return (
    <div className={classes.weatherContainer}>
      <div className={classes.header}>
        <h1>Weather App</h1>
        <LightModeButton theme={theme} toggleTheme={toggleTheme} />
      </div>
      {error && <p>{error}</p>}
      <div className={classes.weatherInfo}>
        {weather && (
          <div className={classes.todaysWeatherInfo}>
            <h2>{weather.name}</h2>
            <div>
              <p>Condition: {weather.weather[0].description}</p>
              <p>
                <p>
                  Feels like:{" "}
                  {isCelsius
                    ? weather.main.temp
                    : convertCelsiusToFahrenheit(weather.main.feels_like)}
                  °{isCelsius ? "C" : "F"}
                </p>
                Temperature:{" "}
                {isCelsius
                  ? weather.main.temp
                  : convertCelsiusToFahrenheit(weather.main.temp)}
                °{isCelsius ? "C" : "F"}
              </p>
              <Image
                src={`http://openweathermap.org/img/wn/${weather.weather[0]?.icon}.png`}
                alt="Weather Icon"
                width={50}
                height={50}
              />
            </div>
          </div>
        )}

        {forecast && (
          <div className={classes.forecastInfo}>
            <div>
              <h2>5-Day Forecast</h2>
              <div>
                <p>
                  {forecast.city.name}, {forecast.city.country}
                </p>
                <p>
                  Sunrise:
                  {new Date(forecast.city.sunrise * 1000).toLocaleTimeString(
                    "en-US",
                    {
                      hour: "2-digit",
                      minute: "2-digit",
                    }
                  )}
                </p>
                <p>
                  Sunset:
                  {new Date(forecast.city.sunset * 1000).toLocaleTimeString(
                    "en-US",
                    {
                      hour: "2-digit",
                      minute: "2-digit",
                    }
                  )}
                </p>
              </div>
              <button onClick={() => setIsCelsius(!isCelsius)}>
                Switch to {isCelsius ? "Fahrenheit" : "Celsius"}
              </button>
            </div>
            <div className={classes.forecastList}>
              {forecast.list
                .filter((_, index) => index % 8 === 0) // Show once per day
                .map((day: Forecast, idx) => (
                  <div key={idx} className={classes.forecastItem}>
                    <p className={classes.forecastItemDay}>
                      {new Date(day.dt_txt).toLocaleDateString("en-US", {
                        weekday: "long",
                      })}
                    </p>
                    <p>
                      Temp:{" "}
                      {isCelsius
                        ? day.main.temp
                        : convertCelsiusToFahrenheit(day.main.temp)}
                      °{isCelsius ? "C" : "F"}
                    </p>
                    <p>{day.weather[0]?.description}</p>
                    <Image
                      src={`http://openweathermap.org/img/wn/${day.weather[0]?.icon}.png`}
                      alt="Weather Icon"
                      width={50}
                      height={50}
                    />
                  </div>
                ))}
            </div>
          </div>
        )}
      </div>
    </div>
  );
}
